@use 'root-rhythm';

.menu-button {
  margin-right: root-rhythm.rr-gridbase(2);
  padding: 0;
  border-width: 0;
  width:auto;
  background-color: transparent;
  color: var(--menu-icon-color);

  &:hover,
  &:active {
    background-color: transparent;
    color: var(--menu-icon-color-active);
  }
}