// Carousel styles are set in the slick theme file

//The component (i.e. this file) should encapsulate the slick library, 
//but I'm not sure if the slick styes are used in other components as well, so we'll
//leave this as-is for now.

.carousel {
    & figcaption a {
        @include mixins.link-colors($d: var(--link), $h: var(--link));
    }
}