$breakpoint-s: 35rem;
$breakpoint-m: 48rem;
$breakpoint-l: 58rem;
$breakpoint-xl: 76rem;

:root {
    /* Default (mobile-first) grid settings */
    --grid-columns: 4;
    --grid-gutter: calc(1rem / 2); /* 0.5rem */
    --grid-offset: 1rem;
    --grid-vspace: 2rem;
    --grid-column-color: rgba(200, 200, 255, .2);
    --grid-baseline-color: rgba(128, 128, 200, .2);
    --gridbase: calc(1rem / var(--rr-grid-divisions));
  }
  
  @media (min-width: $breakpoint-m) {
    :root {
      --grid-columns: 8;
      --grid-gutter: 1rem;
      --grid-offset: 2rem;
      --grid-vspace: 3rem;
      --grid-column-color: rgba(255, 200, 200, .2);
    }
  }
  
  @media (min-width: $breakpoint-l) {
    :root {
      --grid-columns: 12;
      --grid-gutter: 2rem;
      --grid-vspace: 5rem;
      --grid-column-color: rgba(128, 255, 200, .2);
    }
  }
  
  @media (min-width: $breakpoint-xl) {
    :root {
      --grid-columns: 12;
      --grid-offset: 0;
      --grid-gutter: 2rem;
      --grid-column-color: rgba(200, 128, 255, .2);
    }
  }
  