.checker-divider::after {
  max-width: 72rem;
  margin: 0 auto;
  display: block;
  width: 100%;
  font-family: "iconfont";
  content: "\ea09";
  color: #fff;
  font-style: normal;
  font-weight: normal;
  line-height: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 40px;
  text-align: right;
  position: relative;
  bottom: -1rem;
  padding: 0 1rem;
}
.checker-divider::after > :first-child {
  margin-top: 0;
}
.checker-divider::after > :last-child {
  margin-bottom: 0;
}
